import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NetworkService {
  private API_SERVER = "http://localhost:5000"+"/api"

  constructor(private httpClient: HttpClient) { }


  async isLoggedIn() {
    this.sendGetRequest(
      'checkIsLoggedIn',
      {
        "authorization": localStorage.getItem('token'),
      },
    ).subscribe(
      response => {

      },
      error =>{
        localStorage.clear();
        //this.isAuthenticated.next(error.is_authenticated);
      }
    )
  }

  public sendPostRequest(endpoint: string, body: any, headers?: any): Observable<any> {
    let options = {};

    if(headers) {
      options['headers'] = headers;
    } else {
      options['headers'] = {};
    }

    options['withCredentials'] = true;

    return this.httpClient.post(`${this.API_SERVER}/${endpoint}`, body);

  }


  public sendGetRequest(endpoint: string, params?: any, headers?: any): Observable<any> {
    let options = {}

    if(headers) {
      options['headers'] = headers;
    } else {
      options['headers'] = {}
    }

    if(params) {
      options['params'] = params;
    }

    options['withCredentials'] = false;

    return this.httpClient.get(`${this.API_SERVER}/${endpoint}`, options);
  }

}
