import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  _form: FormGroup;
  public registerInvalid: boolean;
  private formSubmitAttempt: boolean;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private authService: AuthService
  ) { 
    this.createForm();
  }

  ngOnInit(): void {
  }

  createForm() {
    this._form = this.fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  async registerUser() {
    this.registerInvalid = false;
    this.formSubmitAttempt = false;

    if (this._form.valid) {
      const username = this._form.get('username').value;
      const password = this._form.get('password').value;

      // console.log(username)

      await this.authService.registerUser(username, password, this._form).then(
        response => {
          this.authService.loginUser(username, password, this._form).then(
          response => {
            localStorage.setItem('token', response.auth_token);
  
            this.router.navigate(['/ranking']);
          },
          error => {
            // console.log("nao logou");
          }
        );
          // this.router.navigate(['/login']);
        },
        error => {
          // console.log("nao logou");
          this.registerInvalid = true;
        }
      );
    } else {
      this.formSubmitAttempt = true;
    }
  }

}
